import React from 'react';
import styles from './Posts.module.css';
import Post from './Post/Post';

const Posts = (props) => {
    let postsList = props.postsList;

    let transformedPostsList = postsList.map((post, index) => (
        <Post
            key={index}
            id={post.id}
            post={post.post}
            likes_amount={post.likes_amount}
        ></Post>
    ));

    return <div className={styles.profile__posts}>{transformedPostsList}</div>;
};

export default Posts;
