import React from 'react';
import { NavLink } from 'react-router-dom';
import styles from './Dialogs.module.css';
import DialogsUser from './DialogsUser/DialogsUser';
import Message from './Message/Message';

const Dialogs = (props) => {
    console.log(props);
    let transformedUserData = props.usersData.map((dialog, index) => {
        let path = '/dialogs/' + dialog.id;

        return (
            <NavLink
                key={index}
                to={path}
                className={styles.dialogs__user_link}
                activeClassName={styles.dialogs__user_active_link}
            >
                <DialogsUser id={dialog.id} name={dialog.name}></DialogsUser>
            </NavLink>
        );
    });

    let transformedMessagesData = props.messagesData.map((message, index) => (
        <Message
            key={index}
            id={message.id}
            message={message.message}
        ></Message>
    ));

    return (
        <div className={styles.dialogs}>
            <div className={styles.dialogs__users_column}>
                <div className={styles.dialogs__users}>
                    {transformedUserData}
                </div>
            </div>
            <div className={styles.dialogs__messages_column}>
                {transformedMessagesData}
            </div>
        </div>
    );
};

export default Dialogs;
